import React from "react";
import "./style.scss";

class NavBar extends React.Component {
	state = {
		isMenuClicked: false
	};

	handleOnClick = (e) => {
		e.preventDefault();
		if (this.state.isMenuClicked) {
			this.setState({
				isMenuClicked: false
			});
		} else {
			this.setState({
				isMenuClicked: true
			});
		}
	};

	render() {
		const { isInExhibition = false, history } = this.props;
		return (
			<nav>
				<div className='navbar'>
					<div className='burger_menu' onClick={this.handleOnClick}>
						<div className='bar' />
						<div className='bar' />
						<div className='bar' />
					</div>
				</div>
				<div
					className={`navbar__menu ${this.state.isMenuClicked ? "on" : ""}`}
					style={{
						marginTop: isInExhibition ? `50px` : `0px`
					}}
				>
					<div className='navbar__menu__list'>
						<a
							className='navbar__menu__list__item'
							onClick={() => {
								history.push("/home");
							}}
						>
							首頁
						</a>
						<a className='navbar__menu__list__item'>個人頁面</a>
					</div>
				</div>
			</nav>
		);
	}
}

export default NavBar;
