import React from "react";
import "./style.scss";

class ExhibitCard extends React.Component {
	handleOnClick = () => {
		const { history } = this.props;
		history.push("./exhibition");
	};

	render() {
		const { imgPath, title, artist, viewAmount, markAmount } = this.props;
		return (
			<div className='exhib_card' onClick={this.handleOnClick}>
				<div className='exhib_card__pic'>
					<img src={imgPath} alt='pic' />
				</div>
				<div className='exhib_card__text'>
					<div className='exhib_card__text__title'>{title}</div>
					<div className='exhib_card__text__artist'>{artist}</div>
					<div className='exhib_card__text__numbers'>
						<div className='exhib_card__text__numbers__item view'>{viewAmount}</div>
						<div className='exhib_card__text__numbers__item mark'>{markAmount}</div>
					</div>
				</div>
			</div>
		);
	}
}

export default ExhibitCard;
