import React from "react";
import "./style.scss";
import NavBar from "../NavBar";
import TopBar from "./TopBar";
import Footer from "../Footer";
import { Switch, Route, Redirect } from "react-router-dom";
import Works from "./Works";
import ThreeD from "./ThreeD";
import Info from "./Info";
import Team from "./Team";

class Exhibition extends React.Component {
	constructor(props) {
		super(props);
		this.routes = [
			{
				path: "/exhib_info",
				component: Info,
				index: 0
			},
			{
				path: "/exhib_3d",
				component: ThreeD,
				index: 1
			},
			{
				path: "/exhib_works",
				component: Works,
				index: 2
			},
			{
				path: "/exhib_team",
				component: Team,
				index: 3
			}
		];

		const currentRoute = this.routes.filter((route) => props.location.pathname.includes(route.path))[0];

		this.state = {
			selectedValue: (currentRoute ? currentRoute : this.routes[2]).path
		};
	}

	selectedIndexOnChange = (e) => {
		const { history, match } = this.props;
		const targetValue = e.target.value;

		this.setState(
			(state) => ({
				...state,
				selectedValue: targetValue
			}),
			() => history.replace(`${match.url}/${targetValue}`)
		);
	};
	render() {
		const { match, history } = this.props;
		return (
			<div>
				<NavBar isInExhibition={true} history={history} />
				<TopBar title='致女兒' onChange={this.selectedIndexOnChange} />
				<Switch>
					<Route path={`${match.url}/exhib_info`} component={Info} />
					<Route path={`${match.url}/exhib_3d`} component={ThreeD} />
					<Route path={`${match.url}/exhib_works`} component={Works} />
					<Route path={`${match.url}/exhib_team`} component={Team} />
					<Redirect from={`${match.url}`} to={`${match.url}/exhib_works`} />
				</Switch>
				<Footer />
			</div>
		);
	}
}

export default Exhibition;
