import React from "react";
import "./style.scss";

class TopBar extends React.Component {
	render() {
		const { title, onChange } = this.props;

		return (
			<div className='exhib_top_bar'>
				<p className='exhib_top_bar__title'>{title}</p>
				<a className='exhib_top_bar__mark' />
				<select
					className='exhib_top_bar__select_page'
					name='exhib_page'
					defaultValue='exhib_works'
					onChange={onChange}
				>
					<option value='exhib_info'>展覽資訊</option>
					<option value='exhib_3d'>3D 展間</option>
					<option value='exhib_works'>作品導覽</option>
					<option value='exhib_team'>策展團隊</option>
				</select>
			</div>
		);
	}
}

export default TopBar;
